import { IonSpinner } from '@ionic/react'
import React, { useEffect, useRef, useState } from 'react'
import { observer } from 'mobx-react-lite'
import './ClienteDireccionUpdate.css'
import Leaflet, { LatLngTuple } from 'leaflet'
import { useStores } from '../../models/root-store/root-store-context'
import { AdminItemField, Header, HeaderCompacto, Page, SelectorModal, SMap } from '../../components'
import { autocompletar_calles, setearSoloCalle, useApiToaster, useToaster, useTryGoBack } from '../../helpers-ts'
import { RUTAS } from '../../constants'
import { Geolocation } from '@capacitor/geolocation'
import MapaSelectorModal from '../../components/MapaSelectorModal'
import { SubHeader } from '../../components/SubHeader'

Leaflet.Icon.Default.imagePath = 'images/'

type ClienteDireccionUpdateProps = {}

const defaultValues = {
  id: 0,
  usuario: 0,
  nombre: '',
  calle_principal: null as any,
  calle_secundaria: null as any,
  numero: '',
  referencia: '',
  ubicacion: '',
  default: false,
  ciudad: '',
}

// @ts-ignore
export const ClienteDireccionUpdate: React.FC<ClienteDireccionUpdateProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { adminStore } = rootStore
  const [Loading, setLoading] = useState(false)
  const [Errors, setErrors] = useState(defaultValues)
  const [Datos, setDatos] = useState(defaultValues)
  const [Ready, setReady] = useState(false)
  const apiToaster = useApiToaster()
  const addToast = useToaster()
  const tryGoBack = useTryGoBack()

  // Ubicación
  const MapaRef = useRef() // Para el modal
  const [Center, setCenter] = useState({ lng: -57.63591, lat: -25.30066 } as any)
  const [Lat, setLat] = useState<number | null>(null)
  const [Lng, setLng] = useState<number | null>(null)
  const [UbicacionUser, setUbicacionUser] = useState<LatLngTuple | null>(null)

  const setup = async () => {
    obtenerCiudades()
    // Timeout para renderizar correctamente el mapa
    setTimeout(() => {
      setReady(true)
    }, 700)
    //
    if (adminStore.direccionDetalle) {
      if (!!adminStore.direccionDetalle.ubicacion) {
        const ubicacion = adminStore.direccionDetalle.ubicacion.split(',')
        // @ts-ignore
        setLat(parseFloat(ubicacion[0]))
        // @ts-ignore
        setLng(parseFloat(ubicacion[1]))
        setCenter({ lat: parseFloat(ubicacion[0]), lng: parseFloat(ubicacion[1]) })
      }
      const datos = { ...adminStore.direccionDetalle } as any
      datos.calle_principal = { label: datos.calle_principal, value: datos.calle_principal }
      datos.calle_secundaria = { label: datos.calle_secundaria, value: datos.calle_secundaria }
      setDatos(datos)
    }
  }

  useEffect(() => {
    // Evitamos usar useIonViewWillEnter porque se ejecuta cada vez que se entra a una vista
    setup()
  }, [])

  useEffect(() => {
    if (rootStore.hasLocationPermission) {
      actualizarUbicacionUser().catch((error) => {
        console.log(error)
        if (String(error).includes('location disabled')) {
          addToast('La ubicación está desactivada.')
        }
      })
    }
  }, [rootStore.hasLocationPermission])

  const actualizarUbicacionUser = async () => {
    const res = await Geolocation.getCurrentPosition({
      enableHighAccuracy: true,
      timeout: 15000,
      maximumAge: 60 * 60 * 1000,
    })
    setUbicacionUser([res.coords.latitude, res.coords.longitude])
    setLat(res.coords.latitude)
    setLng(res.coords.longitude)
    setCenter({ lat: res.coords.latitude, lng: res.coords.longitude })
  }

  const obtenerCiudades = () => {
    if (!rootStore.ciudades.length) {
      setLoading(true)
      rootStore.actualizarDatosInicio(false).then(() => {
        setLoading(false)
      })
    }
  }

  const guardar = () => {
    setLoading(true)
    setErrors(defaultValues)
    const ubicacion = Lat + ',' + Lng
    const calle_principal = Datos.calle_principal
    const calle_secundaria = Datos.calle_secundaria
    const datos = {
      ...Datos,
      ubicacion: ubicacion,
      calle_principal: calle_principal ? calle_principal.value : '',
      calle_secundaria: calle_secundaria ? calle_secundaria.value : '',
    }
    if (datos.ciudad && typeof datos.ciudad === 'object') {
      datos.ciudad = (datos.ciudad as any).id
    }
    if (adminStore.clienteDetalle && adminStore.clienteDetalle.id) {
      datos.usuario = adminStore.clienteDetalle.id
    }
    rootStore.environment.api.guardarDireccion(datos).then((result: any) => {
      setLoading(false)
      if (!adminStore.clienteDetalle) {
        return
      }
      apiToaster(result, 'Dirección guardada correctamente', 'guardar')
      if (result.kind === 'ok') {
        const direcciones = [...adminStore.clienteDetalle.direcciones]
        if (datos.id) {
          const direccion = direcciones.find((pret) => pret.id === datos.id)
          if (direccion) {
            // @ts-ignore
            direcciones[direcciones.indexOf(direccion)] = datos
            adminStore.clienteDetalle.setDirecciones(direcciones)
          }
        } else {
          datos.id = result.direccion.id
          // @ts-ignore
          direcciones.push(datos)
          adminStore.clienteDetalle.setDirecciones(direcciones)
        }
        history.push(RUTAS.CLIENTE)
      } else if (result.kind === 'bad-data') {
        setErrors(result.errors)
      }
    })
  }

  const setDato = (campo: string, valor: string) => {
    let datos = { ...Datos }
    // @ts-ignore
    datos[campo] = valor
    setDatos(datos)
  }

  const seleccionarUbicacion = (lat: any, lng: any) => {
    setLat(lat)
    setLng(lng)
    setCenter([lat, lng])
  }

  useEffect(() => {
    if ((Ready || !adminStore.direccionDetalle) && Lat && Lng) {
      rootStore.environment.api.infoCalle(Lat, Lng).then((result: any) => {
        if (result.kind === 'ok') {
          if (result.response.info) {
            const i = result.response.info
            const dato: any = { ...Datos }
            if (i.calle) {
              dato.calle_principal = { value: i.calle, label: i.calle }
            }
            if (i.ciudad_obj) {
              dato.ciudad = rootStore.ciudades.find((c) => c.id === i.ciudad_obj.id)
            }
            if (i.secondary) {
              dato.calle_secundaria = { value: i.secondary, label: i.secondary }
            }
            setDatos(dato)
          }
        } else {
          console.log('error infoCalle', result)
        }
      })
    }
  }, [Lat, Lng])

  const dataSelectorModal = (data: any) => {
    setDato('ciudad', data[0])
  }

  const setDatoInput = (campo: string, data: any) => {
    setDato(campo, data)
  }

  const header = !history.location.checkout ? <Header /> : <HeaderCompacto />

  return (
    <Page header={header} login_required={true} solo_cliente={true}>
      <SubHeader />
      <MapaSelectorModal ref={MapaRef} lat={Lat} lng={Lng} onAccept={seleccionarUbicacion} />
      <div className="page-wrapper" style={{ display: 'flex' }}>
        <div className="container">
          <div className={'card'}>
            <div className="card-body">
              <h3>Mi dirección</h3>
              <p>
                Ingrese la dirección de su casa o destino deseado para la entrega de los pedidos realizados en la
                plataforma.
              </p>
              <div className={'row'}>
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Nombre'}
                  tipo={'texto'}
                  campo={'nombre'}
                  valor={Datos ? Datos.nombre : ''}
                  error={Errors && Errors.nombre}
                  set_dato={setDatoInput}
                  columnas={12}
                  columnas_sm={6}
                />
                <div className={'col-12'}>
                  <div className={'row my-2'}>
                    <div className={'col-12 col-lg-6'}>
                      <label className={'font-weight-bold text-primary'}>Seleccione la ubicación</label>
                    </div>
                    {Ready && (
                      <div className={'col-12'} style={{ height: 300, width: '100%', cursor: 'pointer' }}>
                        <SMap
                          lat={Lat}
                          lng={Lng}
                          zoom={Lat ? 16 : 14}
                          center={Center}
                          bloquear_interaccion
                          onClick={() => {
                            setTimeout(() => {
                              ;(MapaRef.current as any).open()
                            }, 300)
                          }}
                        />
                      </div>
                    )}
                  </div>
                </div>

                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Calle principal'}
                  tipo={'select2async'}
                  campo={'calle_principal'}
                  valor={Datos ? Datos.calle_principal : ''}
                  error={Errors && Errors.calle_principal}
                  set_dato={(campo: string, data: any) => {
                    if (!data) {
                      return
                    }
                    setDato(campo, data)
                  }}
                  select_placeholder={'Calle principal'}
                  select_onchange={(valor: any) => {
                    setearSoloCalle(rootStore, setDato, setDatos, Datos, 'calle_principal', valor)
                  }}
                  opciones_select={(text: string) => autocompletar_calles(rootStore, UbicacionUser, text)}
                  select_no_options_text={'No se encuentran sugerencias'}
                  columnas={12}
                  columnas_sm={6}
                />
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Calle secundaria'}
                  tipo={'select2async'}
                  campo={'calle_secundaria'}
                  valor={Datos ? Datos.calle_secundaria : ''}
                  error={Errors && Errors.calle_secundaria}
                  set_dato={(campo: string, data: any) => {
                    if (!data) {
                      return
                    }
                    setDato(campo, data)
                  }}
                  select_placeholder={'Calle secundaria'}
                  select_onchange={(valor: any) => {
                    setearSoloCalle(rootStore, setDato, setDatos, Datos, 'calle_secundaria', valor)
                  }}
                  opciones_select={(text: string) => autocompletar_calles(rootStore, UbicacionUser, text)}
                  select_no_options_text={'No se encuentran sugerencias'}
                  columnas={12}
                  columnas_sm={6}
                />
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Número de casa'}
                  tipo={'texto'}
                  campo={'numero'}
                  valor={Datos ? Datos.numero : ''}
                  error={Errors && Errors.numero}
                  set_dato={setDatoInput}
                  columnas={12}
                  columnas_sm={6}
                />
                <AdminItemField
                  deshabilitado={!Loading}
                  titulo={'Referencia'}
                  tipo={'textarea'}
                  campo={'referencia'}
                  valor={Datos ? Datos.referencia : ''}
                  error={Errors && Errors.referencia}
                  set_dato={setDatoInput}
                  columnas={12}
                />
              </div>
              <SelectorModal
                titulo={'Ciudad'}
                titulo_label={'Seleccioná la ciudad'}
                listado_elementos={rootStore.ciudades}
                // @ts-ignore
                listado_elementos_seleccionados={
                  !Datos['ciudad'] && adminStore.direccionDetalle
                    ? adminStore.direccionDetalle.ciudad
                    : [Datos['ciudad']]
                }
                datos_filtrados={dataSelectorModal}
                multiple={false}
                llave_label_opcion={'nombre'}
              />
              {Errors.ciudad && <div className={'text-danger'}>{Errors.ciudad}</div>}
              <div className={'form-group'}>
                <input
                  type={'checkbox'}
                  id={'aprobado'}
                  checked={Datos.default}
                  onClick={(evt) => {
                    // @ts-ignore
                    setDato('default', !Datos.default)
                  }}
                />
                <label
                  id={'aprobado'}
                  onClick={(evt) => {
                    // @ts-ignore
                    setDato('default', !Datos.default)
                  }}
                >
                  Dirección por defecto
                </label>
              </div>
              <div className={'form-group text-right mt-3'}>
                <a onClick={() => tryGoBack(RUTAS.CLIENTE)} className={'btn btn-light'}>
                  Cancelar
                </a>
                <button className={'btn btn-primary ml-2'} disabled={Loading} onClick={guardar}>
                  {Loading && <IonSpinner name="crescent" />}
                  {!Loading && <span>Guardar</span>}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default ClienteDireccionUpdate
